










































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CWeb from '@/components/links/CWeb.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';

@Component({
  components: {
    CWeb,
    CSelectorSample,
  },
  head: {
    title: {
      inner: 'URLエンコーダ',
    },
  },
})
export default class UrlEncoder extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataEnDecode: string = 'encode';
  private dataOrgUrl: string = '';
  private dataResultUrl: string = '';
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'wikipedia',
      caption: 'ウィキペディア（エンコード用）',
      content: 'https://ja.wikipedia.org/wiki/メインページ',
    },
    {
      name: 'wikipedia',
      caption: 'ウィキペディア（デコード用）',
      content: 'https://ja.wikipedia.org/wiki/%E3%83%A1%E3%82%A4%E3%83%B3%E3%83%9A%E3%83%BC%E3%82%B8',
    },
  ];
  private dataSampleIndex: number = 0;

  // Watch
  @Watch('dataEnDecode')
  private watchOnEnDecodeSelected(newValue: string, orgValue: string) {
    this.endecodeUrl();
  }
  @Watch('dataOrgUrl')
  private watchOnTagChecked(newValue: string, orgValue: string) {
    this.endecodeUrl();
  }

  // Computed
  private get computedIsDisabled(): boolean {
    return this.dataOrgUrl === '';
  }

  // Methods
  private listenEnDecodeSelect($event: Event): void {
    this.dataEnDecode = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnSampleSelect($event: Event): void {
    this.dataOrgUrl = this.dataSamples[Number($event)].content;
  }
  private listenInput($event: Event): void {
    this.dataOrgUrl = (<HTMLInputElement>(<any>$event).target).value;
  }
  private endecodeUrl(): void {
    switch (this.dataEnDecode) {
      case 'encode':
        this.dataResultUrl = encodeURI(this.dataOrgUrl);
        break;
      case 'decode':
        this.dataResultUrl = decodeURI(this.dataOrgUrl);
        break;
    }
  }
}
